import React, { useState } from "react";
import {
  MDBNavbar,
  MDBNavbarBrand,
  MDBNavbarNav,
  MDBNavItem,
  MDBNavLink,
  MDBNavbarToggler,
  MDBCollapse,
} from "mdbreact";
import { HashRouter as Router } from "react-router-dom";

const Menu = () => {
  const [isOpen, setIsOpen] = useState(false);

  const toggleCollapse = () => {
    setIsOpen(!isOpen);
  };

  return (
    <Router>
      <MDBNavbar
        color='light-green lighten-1'
        dark
        expand='md'
        className='justify-content-center'
      >
        <MDBNavbarBrand>
          <MDBNavLink to='/'>
            <strong className='white-text'>LearnIt</strong>
          </MDBNavLink>
        </MDBNavbarBrand>
        <MDBNavbarToggler onClick={toggleCollapse} />
        <MDBCollapse id='navbarCollapse3' isOpen={isOpen} navbar>
          <MDBNavbarNav left>
            <MDBNavItem>
              <MDBNavLink to='/question'>Add Question</MDBNavLink>
            </MDBNavItem>
            <MDBNavItem>
              <MDBNavLink to='/department'>Add Department</MDBNavLink>
            </MDBNavItem>
            <MDBNavItem>
              <MDBNavLink to='/course'>Add Course</MDBNavLink>
            </MDBNavItem>
          </MDBNavbarNav>
        </MDBCollapse>
      </MDBNavbar>
    </Router>
  );
};

export default Menu;
