import React from "react";
import {
  MDBContainer,
  MDBRow,
  MDBCol,
  MDBInput,
  MDBBtn,
  MDBCard,
  MDBCardBody,
} from "mdbreact";
import Layout from "../core/Layout";

const AddDepartment = () => {
  const addDepartmentForm = () => (
    <MDBContainer>
      <MDBRow center>
        <MDBCol md='6'>
          <MDBCard style={{ marginTop: "5rem", marginBottom: "5rem" }}>
            <MDBCardBody>
              <form>
                <p className='h4 text-center py-4'>Add Department</p>
                <div className='grey-text'>
                  <MDBInput
                    label='Your name'
                    icon='user'
                    group
                    type='text'
                    validate
                    error='wrong'
                    success='right'
                  />
                  <MDBInput
                    label='Your email'
                    icon='envelope'
                    group
                    type='email'
                    validate
                    error='wrong'
                    success='right'
                  />
                  <MDBInput
                    label='Confirm your email'
                    icon='exclamation-triangle'
                    group
                    type='text'
                    validate
                    error='wrong'
                    success='right'
                  />
                  <MDBInput
                    label='Your password'
                    icon='lock'
                    group
                    type='password'
                    validate
                  />
                </div>
                <div className='text-center py-4 mt-3'>
                  <MDBBtn color='light-green lighten-1' type='submit'>
                    Register
                  </MDBBtn>
                </div>
              </form>
            </MDBCardBody>
          </MDBCard>
        </MDBCol>
      </MDBRow>
    </MDBContainer>
  );

  return <Layout>{addDepartmentForm()}</Layout>;
};

export default AddDepartment;
