import React, { useState } from "react";
import {
  MDBContainer,
  MDBRow,
  MDBCol,
  MDBInput,
  MDBBtn,
  MDBCard,
  MDBCardBody,
  MDBIcon,
} from "mdbreact";
import Layout from "../core/Layout";
import { createQuestion } from "../api";

const AddQuestion = () => {
  const [values, setValues] = useState({
    question: "",
    answer: "",
    a: "",
    b: "",
    c: "",
    d: "",
    explanation: "",
    topic: "",
    course: "",
    level: "",
    error: "",
    loading: "",
    success: "",
  });

  const {
    question,
    answer,
    a,
    b,
    c,
    d,
    explanation,
    topic,
    course,
    level,
    error,
    loading,
    success,
  } = values;

  const handleChange = (name) => (event) => {
    setValues({ ...values, error: false, [name]: event.target.value });
  };

  const clickSubmit = (e) => {
    e.preventDefault();
    createQuestion({
      question,
      answer,
      a,
      b,
      c,
      d,
      explanation,
      topic,
      course,
      level,
    }).then((data) => {
      if (data.error) {
        setValues({ ...values, error: data.error, success: false });
      } else {
        setValues({
          ...values,
          question: "",
          answer: "",
          a: "",
          b: "",
          c: "",
          d: "",
          explanation: "",
          topic: "",
          course: "",
          level: "",
          error: "",
          success: true,
        });
      }
    });
  };

  const addQuestionForm = () => (
    <MDBContainer>
      <MDBRow center>
        <MDBCol md='6'>
          <MDBCard style={{ marginTop: "5rem", marginBottom: "5rem" }}>
            <MDBCardBody>
              <form onSubmit={clickSubmit}>
                <p className='h4 text-center py-4'>Add Question</p>
                <div className='grey-text'>
                  <MDBInput
                    label='Question'
                    icon='question-circle'
                    group
                    type='text'
                    validate
                    error='wrong'
                    success='right'
                    required
                    onChange={handleChange("question")}
                    value={question}
                  />
                  <MDBInput
                    label='Answer'
                    icon='pen'
                    group
                    type='text'
                    validate
                    error='wrong'
                    success='right'
                    required
                    onChange={handleChange("answer")}
                    value={answer}
                  />
                  <MDBInput
                    label='Option A'
                    icon='check-square'
                    group
                    type='text'
                    validate
                    error='wrong'
                    success='right'
                    required
                    onChange={handleChange("a")}
                    value={a}
                  />
                  <MDBInput
                    label='Option B'
                    icon='check-square'
                    group
                    type='text'
                    validate
                    error='wrong'
                    success='right'
                    required
                    onChange={handleChange("b")}
                    value={b}
                  />
                  <MDBInput
                    label='Option C'
                    icon='check-square'
                    group
                    type='text'
                    validate
                    error='wrong'
                    success='right'
                    required
                    onChange={handleChange("c")}
                    value={c}
                  />
                  <MDBInput
                    label='Option D'
                    icon='check-square'
                    group
                    type='text'
                    validate
                    error='wrong'
                    success='right'
                    required
                    onChange={handleChange("d")}
                    value={d}
                  />
                  <MDBInput
                    label='Explanation'
                    icon='paperclip'
                    group
                    type='textarea'
                    row='2'
                    validate
                    error='wrong'
                    success='right'
                    required
                    onChange={handleChange("explanation")}
                    value={explanation}
                  />
                  <MDBInput
                    label='Topic'
                    icon='info-circle'
                    group
                    type='text'
                    validate
                    error='wrong'
                    success='right'
                    required
                    onChange={handleChange("topic")}
                    value={topic}
                  />
                  <MDBInput
                    label='Course'
                    icon='book-open'
                    group
                    type='text'
                    validate
                    error='wrong'
                    success='right'
                    required
                    onChange={handleChange("course")}
                    value={course}
                  />
                  <MDBInput
                    label='Level'
                    icon='user-tie'
                    group
                    type='number'
                    validate
                    error='wrong'
                    success='right'
                    required
                    onChange={handleChange("level")}
                    value={level}
                  />
                </div>
                <div className='text-center py-4 mt-3'>
                  <MDBBtn color='light-green lighten-1' type='submit'>
                    Add
                    <MDBIcon icon='plus' className='ml-2' />
                  </MDBBtn>
                </div>
              </form>
            </MDBCardBody>
          </MDBCard>
        </MDBCol>
      </MDBRow>
    </MDBContainer>
  );

  return <Layout>{addQuestionForm()}</Layout>;
};

export default AddQuestion;
