import React from "react";
import Layout from "./Layout";
import {
  MDBCard,
  MDBCardBody,
  MDBCardTitle,
  MDBCardText,
  MDBCardHeader,
  MDBContainer,
  MDBRow,
  MDBCol,
} from "mdbreact";

const Home = () => {
  return (
    <Layout>
      <MDBContainer fluid>
        <MDBRow center>
          <MDBCol md='6'>
            <MDBCard style={{ marginTop: "5rem" }}>
              <MDBCardHeader
                color='light-green lighten-1'
                className='text-center'
              >
                LearnIt API
              </MDBCardHeader>
              <MDBCardBody>
                <MDBCardTitle className='text-center'>GuideLines</MDBCardTitle>
                <MDBCardText>
                  This is a quick build for the LearnIt API, made to enable easy
                  population of the LearnIt DB
                </MDBCardText>
                <br />
                <MDBCardTitle className='text-center'>Usage</MDBCardTitle>
                <MDBCardText>
                  This is a quick build for the LearnIt API, made to enable easy
                  population of the LearnIt DB
                </MDBCardText>
              </MDBCardBody>
            </MDBCard>
          </MDBCol>
        </MDBRow>
      </MDBContainer>
    </Layout>
  );
};

export default Home;
